import { reqCarList,reqDeleteGood,reqUpdateCheckedById } from "@/api";

const state={
    cartList:[],
    deleteList:[]
};
const mutations={
    GETCARTLIST(state,cartList){
        state.cartList = cartList;
    },
    DELETEGOOD(state,deleteList){
        state.deleteList = deleteList
    },
    
};
const actions={
    async getCartList({commit}){
        let result = await reqCarList();
        if(result.code == 200){
            commit('GETCARTLIST',result.data);
        }
    },
    async deleteGood({commit},skuId){
        let result = await reqDeleteGood(skuId);
        if(result.code == 200){
            commit('DELETEGOOD',result.data)
        }
    },//改变商品的状态
    async updateCheckById({commit},{skuId,isChecked}){
        let result = await reqUpdateCheckedById(skuId,isChecked);
        if(result.code == 200){
            return 'ok'
        }else{
            return Promise.reject(new Error('faile'))
        }
    },//删除全部选中的产品
    delteAllCheckedCart({dispatch,getters}){
        let PromiseAll=[];
        getters.cartList.cartInfoList.forEach(element => {
            let promise = element.isChecked == 1?dispatch('deleteGood',element.skuId):'';
            PromiseAll.push(promise);
        });
        return Promise.all(PromiseAll)
    },//改变全部商品的选中状态
    updateAllCheckedCart({state,dispatch},isChecked){
        let PromiseAll = [];
        state.cartList[0].cartInfoList.forEach(item=>{
            let promise = dispatch('updateCheckById',{skuId:item.skuId,isChecked})
            PromiseAll.push(promise);
        })
        return Promise.all(PromiseAll);
    }
};
const getters={
    cartList(state){
        return state.cartList[0]||{}
    }
};
export default{
    state,
    mutations,
    actions,
    getters,
}
