import { reqAddressInfo,reqUserGood } from "@/api";

const state={
    userInfo:[],
    goodsInfo:[],

};
const mutations={
    USERADDRESS(state,userInfo){
        state.userInfo = userInfo;
    },
    USERGOOD(state,userGood){
        state.goodsInfo = userGood
    }
};
const actions={
    async userAddress({commit}){
        let result = await reqAddressInfo();
        if(result.code==200){
            commit('USERADDRESS',result.data)
        }

    },
    async userGood({commit}){
        let result = await reqUserGood();
        if(result.code == 200){
            commit('USERGOOD',result.data);
        }
        
    }
};
const getters={

};
export default{
    state,
    mutations,
    actions,
    getters,
}
